import React, {Component} from 'react'
import './App.css';
import {GiPunch} from 'react-icons/gi';
import {FaHandPaper} from  'react-icons/fa'
import {FaHandScissors} from  'react-icons/fa'

class App extends  React.Component {

  constructor(){
    super();

    this.state ={
      playchoice:<FaHandPaper/>,
      computerchoice:<FaHandPaper/>,
      playermark:0,
      compmark:0
    }
    this.HandlePClick=this.HandlePClick.bind(this);
    this.HandleSClick=this.HandleSClick.bind(this);
    this.HandleRClick=this.HandleRClick.bind(this);
  }





  HandlePClick() {
    function CompD(){
      return(Math.floor(Math.random() * 3) + 1)
    };

    var X = CompD();

     

      if(  X === 1){

       
          this.setState({
          playchoice:<FaHandPaper/>,
          computerchoice:<FaHandPaper/>,
          
          
          
        })
      
    }
        
    else if(X ===2){
      this.setState({
        playchoice:<FaHandPaper/>,
        computerchoice:<FaHandScissors/>,
        compmark: this.state.compmark +1,
        
        
      })
    }


    else{
      this.setState({
        playchoice:<FaHandPaper/>,
        computerchoice:<GiPunch/>,
        playermark: this.state.playermark +1,
        
        
      })
    }
         
          
      
   
  };


  HandleSClick() {
    function CompD(){
      return(Math.floor(Math.random() * 3) + 1)
    };

    var X = CompD();

     

      if(  X === 1){

       
          this.setState({
          playchoice:<FaHandScissors/>,
          computerchoice:<FaHandPaper/>,
          playermark: this.state.playermark +1,
          
          
        })
      
    }
        
    else if(X ===2){
      this.setState({
        playchoice:<FaHandScissors/>,
        computerchoice:<FaHandScissors/>,
        
        
        
      })
    }


    else{
      this.setState({
        playchoice:<FaHandScissors/>,
        computerchoice:<GiPunch/>,
        compmark: this.state.compmark +1,
        
        
      })
    }
         
          
      
   
  };

  HandleRClick() {
    function CompD(){
      return(Math.floor(Math.random() * 3) + 1)
    };

    var X = CompD();

     

      if(  X === 1){

       
          this.setState({
          playchoice:<GiPunch/>,
          computerchoice:<FaHandPaper/>,
          compmark: this.state.compmark +1,
          
          
        })
      
    }
        
    else if(X ===2){
      this.setState({
        playchoice:<GiPunch/>,
        computerchoice:<FaHandScissors/>,
        playermark: this.state.playermark +1,
        
        
      })
    }


    else{
      this.setState({
        playchoice:<GiPunch/>,
        computerchoice:<GiPunch/>,
        
        
        
      })
    }
         
          
      
   
  };

  render(){

   


    return (



      <div className="App">
        <h1 className="title">You  VS  Computer</h1>
<div className="P-wrap">
          <div className="PM">{this.state.playermark}</div>
          <div className="PC">{this.state.playchoice}</div>
         
          </div>
          
        <div className="C-wrap">
        <div className="CM">{this.state.compmark}</div>
          
          <div className="CC">{this.state.computerchoice}</div>
          </div>
          
          
        <div className='btn-wrapper'>
        <button   className='btn' onClick={this.HandlePClick}><FaHandPaper/></button>
        <button className='btn'  onClick={this.HandleSClick}><FaHandScissors/></button>
        <button className='btn' onClick={this.HandleRClick}><GiPunch/></button>
        </div>
      </div>
    )
  }
  
}

export default App;
